.controls {
  display: flex;
  justify-content: center;
  width: 100%;

  box-sizing: border-box;
}

.control--wrapper {
  width: 100%;
  height: 170px;
  max-width: 750px;
  min-width: 310px;
  border-radius: 20px;

  box-sizing: border-box;

  &:first-child {
    margin-right: 5px;
  }
}

.control {
  display: flex;
  position: relative;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  border: 1px solid #2A2B2E;
  background-image: url('../../../assets/img/grey-texture-bg.b22cf205f88ab054.png');
  height: 100%;
  margin: 0 auto;

  box-sizing: border-box;


}
.active {
  border: solid 1px #d07206;
}

.cancel {
  border: solid 1px #cb011a;
}

.control-switcher {
  font-size: 12px;
  margin-bottom: auto;
  margin-top: 10px;

  &--inner {
    height: 20px;
    border-radius: 10px;
    border: solid 1px #363738;
  }

  &__item {
    padding: 0;
    width: 100px;
    height: 100%;

    position: relative;
    text-align: center;
    background: none;
    border: none;

    color: #ffffff;

    &:hover {
      color: #d0021b;
      cursor: pointer;
    }

    &.active {
      background: rgb(44, 45, 48);
      border-radius: 10px;
      &:hover {
        color: #ffffff;
        cursor: default;
      }
    }
  }
}

.game-feature {
  margin: 0 auto auto;
  width: calc(100% - 20px);
  max-width: 390px;
  display: flex;
  justify-content: center;

  .bet-block {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 140px;
  }

  .spinner--wrapper {
    height: 34px;
    font-size: 22px;
  }

  .spinner {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: opacity .1s;
    border-radius: 22px;
    background-color: #000000b3;
    border: solid 1px #3C3C42;
    font-weight: 700;
    font-size: inherit;
    line-height: 1;
    padding-left: 10px;
    padding-right: 10px;
  }

  .input {
    display: flex;
    align-items: center;
    width: calc(100% - 35px);
    height: 100%;
    input {
      width: 100%;
      padding: 0;

      font-weight: 700;
      font-size: 22px;
      color: #fff;
      text-align: inherit;

      border: 0;
      background: transparent;
      outline: none;
    }
  }

  .buttons {
    display: inline-flex;
    align-items: center;

    button {
      padding: 0;
      border-style: none;
      width: 17px;
      height: 17px;
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center;

      cursor: pointer;

      &:first-child {
        background-color: transparent;
        background-image: url('../../../assets/img/minus.svg');
      }
      &:last-child {
        margin-left: 3px;
        background-color: transparent;
        background-image: url('../../../assets/img/plus.svg');
      }
    }
  }
}

.bets-opt-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  font-size: 14px;
  color: #ffffffb3;
  width: 100%;

  button {
    border: 1px solid #36363C;
    border-radius: 100px;
    background-color: #252528;
    color: #83878e;
    text-align: center;
    box-shadow: none;

    font-size: 14px;
    width: 64px;
    height: 18px;
    margin-top: 4px;
    line-height: 1;
    padding: 0;

    span {
      font-size: 11px;
      margin-left: 2px;
    }
  }
}

.button-block {
  width: calc(100% - 145px);
  margin-left: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  .active {
    background-color: #d07206;

    box-shadow: 0 10px 15px -10px #d07206,inset 0 1px 1px #ffffff80;

    &:hover {
      background-color: #f58708;
    }
  }

  .cancel {
    background-color: #cb011a;
    box-shadow: 0 10px 15px -10px #cb011a,inset 0 1px 1px #ffffff80;

    &:hover {
      background-color: #f7001f;
    }
  }

  button {
    border: 0;
    background-color: #28a909;
    box-shadow: 0 10px 15px -10px #28a909,inset 0 1px 1px #ffffff80;
    color: #fff;
    text-align: center;
    text-shadow: 0 1px 2px rgba(0,0,0,.5);
    width: 100%;
    height: 100%;
    font-size: 20px;
    border-radius: 20px;
    cursor: pointer;

    &:hover {
      background-color: #36cb12;
    }
  }
}

.custom-coefficient {
  position: fixed;
  right: 50%;
  top: 50%;
  transform: translate(50%, -50%);

  padding: 40px 10px;

  background-color: #fff;

  label {
    display: block;
    margin-bottom: 10px;
  }
  input {
    display: block;
    margin-bottom: 5px;
  }
}

.custom-coefficient-close {
  position: absolute;
  right: 0;
  top: 0;
  width: 30px;
  height: 30px;

  color: #000000;
  font-size: 24px;

  background-color: transparent;
  border: none;

  cursor: pointer;
}

.alert-win {
  position: fixed;
  right: 50%;
  top: 10%;
  transform: translateX(50%);

  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 8px;

  font-size: 32px;
  color: #ffffff;

  border: 1px solid #4FAF0E;
  border-radius: 30px;
  background-color: #214b06;

  .multiplier {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-right: 5px;

    .label {
      margin-bottom: 3px;

      font-size: 16px;
      font-weight: 500;
      color: #e7e6e3;
    }

    .value {
      font-size: 18px;
      font-weight: 600;
      color: #ffffff;
    }
  }

  .win {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 5px 35px;
    margin-right: 5px;

    background-color: #4EB00F;
    border-radius: 30px;
    background-image: url('../../../assets/img/2992E5A9-C97E-448F-A074-58DF31E713F9.svg');
    background-repeat: no-repeat;
    background-position: center left;

    &:before {
      content: '';
      position: absolute;
      right: 0;
      top: 0;

      width: 100%;
      height: 100%;
      display: block;

      //background-color: #000;

      background-image: url('../../../assets/img/2992E5A9-C97E-448F-A074-58DF31E713F9.svg');
      background-repeat: no-repeat;
      background-position: center right;

    }

    .label {
      font-size: 18px;
      font-weight: 600;
    }

    .value {
      font-size: 18px;
      font-weight: 600;

      .usd {
        font-size: 14px;
      }
    }
  }

  .close {
    font-size: 32px;
    font-weight: 600;
    color: #798D74;

    border: none;
    background-color: transparent;
  }
}
