.bets-container {
  position: relative;

  height: 100%;
  width: 100%;
  padding-left: 10px;
  display: flex;
  flex-direction: column;

  color: #ffffff;

  min-width: 410px !important;
  max-width: 410px !important;
}

.bets-nav {
  min-height: 40px;
  max-height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.navigation-switcher {
  display: flex;

  height: 20px;

  border-radius: 10px;
  border: solid 1px #363738;
  box-sizing: border-box;

  &__tab {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    width: 100px;

    color: #fff;
    font-size: 12px;

    border-style: none;
    background: transparent;

    cursor: pointer;

    &:hover {
      color: #d0021b;
    }

    &.react-tabs__tab--selected {
      background: rgb(44, 45, 48);
      border-radius: 10px;
      transition: transform .5s ease;
      &:hover {
        color: #ffffff;
      }
    }
  }
}

.bets-wrapper {
  position: relative;

  display: flex;
  flex-direction: column;
  min-height: 100vh;
  height: 100%;
  background-image: url('../../../assets/img/grey-texture-bg.b22cf205f88ab054.png');
  border-radius: 20px 20px 0 0;
  border: 1px solid #2A2B2E;
  border-bottom-color: rgb(42, 43, 46);
  border-bottom-style: solid;
  border-bottom-width: 1px;
  border-bottom: 0;
  min-height: 0;
}

.bets-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
  height: 24px;
  margin: 10px 8px 6px 0;

  .total-bets {
    padding-left: 10px;

    font-size: 14px;
    span:nth-child(2) {
      color: #427f00;
      font-weight: 700;
    }

  }

  .previous-hand {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 120px;
    height: 24px;
    padding: 0 4px;

    font-size: 12px;
    color: #b8b8b7;

    border-radius: 17px;
    border: solid 1px #6f6f6f;
    cursor: pointer;

    .icon {
      position: relative;
      right: 9px;
      color: #fff;
      width: 17px;
      height: 16px;
      background: url('../../../assets/img/history.svg') no-repeat center center/contain;

    }

    .text {
      box-sizing: border-box;
    }
  }
}

.legend-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 20px;

  font-size: 11px;
  color: #7b7b7b;

  &__user {
    padding-left: 10px;
    box-sizing: border-box;
    width: 28%;
    text-align: left;
  }

  &__bet {
    width: 25%;
    text-align: center;
  }

  &__coeff {
    width: 20%;
    text-align: center;
  }

  &__cash-out {
    width: 20%;
    text-align: center;
  }

  span:last-child {
    width: 7%;
  }
}

.legend-body {
  height: calc(100vh - 138px);

  .legend-row {
    border-radius: 17px;
    background-image: linear-gradient(85deg,#000000,#0f1214);
    border: solid 2px #000000;
    margin-bottom: 2px;
    color: #fff;
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    line-height: 16px;
    align-items: center;
    height: 34px;
  }

  .win {
    background-image: none;
    border: solid 1px #427F00;
    background: #123405;
  }

  .legend-row-user {
    display: flex;
    padding-left: 2px;
    height: 34px;
    width: 28%;
    max-width: 33%;
    flex-shrink: 1;
    flex-grow: 1;
    align-items: center;

    span {
      margin: 0 2px 0 5px;
    }

    .avatar {
      min-width: 30px;
      max-width: 30px;
      min-height: 30px;
      max-height: 30px;
      border-radius: 50%;
    }
  }

  .legend-row-bet {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    width: fit-content;
    max-width: 25%;
    padding: 0 5px;
    border: solid 1px #5e6168;
    border-radius: 30px;
    span {
      .usd {
        margin-left: 2px;
        font-size: 14px;
      }
    }
  }

  .win-bet {
    border: none
  }

  .legend-row-payout {
    width: 20%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;

    span {
      padding: 0 10px;

      color: #afd9ed;

      border-radius: 11px;
      background-color: #005d91;
    }
  }

  .legend-row-cash {
    width: 20%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;

    span {
      color: #fff;
      font-size: 14px;
      line-height: 16px;

      .usd {
        margin-left: 2px;
        font-size: 14px;
      }
    }
  }
}

.footer--wrapper {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 100%;
  display: flex;
  min-height: 25px;
  max-height: 25px;
}

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  background-color: #000;
  border: 1px solid #2A2B2E;
  font-size: 11px;
  color: #5e6269;
  padding: 0 10px;

  * {
    box-sizing: border-box;
  }

  .provably-fair-block {
    display: flex;
    align-items: center;
    height: 16px;
  }

  .provably-fair {
    display: flex;
    cursor: pointer;
    align-items: center;

    .icon {
      background: url('../../../assets/img/BA5F7D89-214B-44BF-93CE-7523ADE19A89.svg') no-repeat center center/contain;
      width: 14px;
      height: 16px;
      margin: 0 4px 0 6px;
    }

    span {
      font-size: 12px;
      color: #bbbfc5;
      line-height: 1px;
    }
  }

  .logo-block {
    display: flex;
    align-items: center;
    height: 14px;

    a {
      color: #007bff;
      text-decoration: none;
      background-color: transparent;
    }

    .logo {
      background: url('../../../assets/img/logo.eba496b407593c0e.svg') no-repeat center center/contain;
      width: 34px;
      height: 14px;
      margin-left: 4px;
      cursor: pointer;
    }
  }
}
