.header {
  display: flex;
  justify-content: space-between;
  height: 37px;

  background-image: url('./../../../assets/img/grey-texture-bg.b22cf205f88ab054.png');

  &__logo {
    margin-left: 10px;
    width: 84px;

    background: url('./../../../assets/img/Aviator-text.svg') no-repeat center center/contain;
  }
}

/* Header Menu */
.header-menu{
  display: flex;
  align-items: center;
}

.header-menu-instruction-btn {
  height: 26px;
  margin-right: 5px;

  color: #5f3816;
  text-align: left;

  background: #e69308 url('./../../../assets/img/8586C5FE-3BCF-459C-9756-F778B2FD4524.svg') no-repeat 10px center;
  border: none;
  border-radius: 20px;

  cursor: pointer;

  span {
    padding-left: 31px;
    padding-right: 10px;
  }
}

.header-menu-user {
  display: flex;

}

.header-menu-user-balance {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 98px;
  height: 26px;
  justify-content: center;

  font-size: 16px;
  font-weight: 700;
  color: #9b9c9e;

  border-radius: 20px;
  border: 1px solid #2A2B2E;

  &__amount {
    //color: #9b9c9e;
  }

  &__icon {
    width: 10px;
    margin-left: 3px;
    font-size: 14px;
  }
}

.header-menu-user-btn {
  width: 42px;
  height: 26px;
  border-radius: 20px;
  margin-left: 5px;
  margin-right: 5px;
  background: #2C2D30 url('./../../../assets/img/burger.svg') no-repeat center center;
  cursor: pointer;
}
