.result-history {
  min-height: 40px;
  max-height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.result-history-stats {
  height: 22px;
  width: 100%;
  display: flex;
  align-items: center;
  position: relative;
  padding: 0 10px;

  .shadow {
    position: absolute;
    right: 68px;
    width: 13px;
    height: 22px;
    background-image: linear-gradient(to left,#0d0d0d,rgba(0,0,0,0));
    z-index: 3;
  }
}

.payouts-button {
  position: absolute;
  right: 0px;
  top: 50%;
  transform: translateY(-50%);
  height: 100%;
  width: 70px;

  background-color: #101011;

  &--inner {
    margin-left: .255em;
    vertical-align: .255em;
    content: "";
    border-top: .3em solid;
    border-right: .3em solid transparent;
    border-bottom: 0;
    border-left: .3em solid transparent;

    margin-left: 6px;
    position: relative;
    z-index: 9;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 46px;
    height: 22px;
    border-radius: 13px;
    border: solid 1px #6f6f6f;
    background-color: #1e1e1e;
    cursor: pointer;
  }

  .payouts-button__history {
    width: 14px;
    height: 13px;
    background: url('../../../assets/img/history.svg') no-repeat center center/contain;
    margin-right: 5px;
  }

  .payouts-button__dd {
    width: 10px;
    height: 10px;
    transition: transform .5s;
    background: url('../../../assets/img/A2700E68-8AC5-4E79-9C20-4A08B32F31D0.svg') no-repeat center center/contain;
  }
}

.payouts-wrapper {
  display: flex;
  align-items: center;
  height: 100%;
  width: 100%;
  overflow: hidden;

  .payout {
    display: flex;
    margin-right: 4px;
    margin-left: 1px;
    padding: 0 13px;

    position: relative;

    border-radius: 30px;
    background-color: #427f00;

    &:nth-child(1) {
      opacity: 1;
    }
    &:nth-child(2) {
      opacity: 0.95;
    }
    &:nth-child(3) {
      opacity: 0.9;
    }
    &:nth-child(4) {
      opacity: 0.85;
    }
    &:nth-child(5) {
      opacity: 0.8;
    }
    &:nth-child(6) {
      opacity: 0.75;
    }
    &:nth-child(7) {
      opacity: 0.7;
    }
    &:nth-child(8) {
      opacity: 0.65;
    }
    &:nth-child(9) {
      opacity: 0.6;
    }
    &:nth-child(10) {
      opacity: 0.55;
    }
    &:nth-child(n+11) {
      opacity: 0.5;
    }


    span {
      height: 22px;

      font-size: 12px;
      line-height: 24px;
      color: #afd9ed;

      cursor: pointer;
    }
  }
}
